import React, { useRef } from 'react'

import { Swiper, SwiperSlide } from 'swiper/react';

import { BuildScene } from '../components'

import { full } from '../styles'

export default function BuildSection(props) {
  const ref = useRef()
  const { scenes, refs } = props
  return (
    <Swiper
      ref={ref}
      spaceBetween={0}
      slidesPerView={1}
      threshold={15}
      //nested
      grabCursor
      navigation
      direction="horizontal"
      style={full}
      pagination={{ clickable: true }}
      scrollbar={{ draggable: true }}
    >
      {
        scenes.map((scene, index) => (
          <SwiperSlide key={`scene${index}`}>
            <BuildScene scene={scene} refs={{ ...refs, section: ref }} />
          </SwiperSlide>
        ))
      }
    </Swiper>
  )
}