import React, { useState } from 'react'

import SwiperCore, {
  Navigation,
  Pagination,
  Scrollbar,
  A11y,
  Autoplay,
  EffectFade,
  Zoom
} from 'swiper';

//import 'swiper/swiper.scss';
//import 'swiper/components/navigation/navigation.scss';
import 'swiper/components/pagination/pagination.scss';
import 'swiper/components/zoom/zoom.scss';
//import 'swiper/components/scrollbar/scrollbar.scss';

import './index.css';
import './swiper.scss';
import './navigation.scss';
import './scrollbar.scss';

import { products } from './assets'

import { BoasVindas, Encartes, Orçamento } from './scenes'

import { Builder } from './components'

import { useDimensions } from './hooks'

SwiperCore.use([
  Navigation,
  Pagination,
  Scrollbar,
  A11y,
  Autoplay,
  EffectFade,
  Zoom
]);

const defaultEncartes = [
  {
    name: 'Cesta Modelo 1',
    details: [
      { name: 'Peso', value: '75 KG' },
      { name: 'Contém', value: '22 itens' },
      { name: 'Preço', value: 'R$ 79,00', highlight: true },
    ],
    images: products.products
  },
  {
    name: 'Cesta Modelo 2',
    details: [
      { name: 'Peso', value: '96 KG' },
      { name: 'Contém', value: '27 itens' },
      { name: 'Preço', value: 'R$ 99,00', highlight: true },
    ],
    images: products.products
  },
  {
    name: 'Cesta Modelo 3',
    details: [
      { name: 'Peso', value: '120 KG' },
      { name: 'Contém', value: '35 itens' },
      { name: 'Preço', value: 'R$ 119,00', highlight: true },
    ],
    images: products.products
  }
]

export default () => {
  const { ref, dimensions } = useDimensions()
  const [orçamentoImages, setOrçamentoImages] = useState([...products.products, ...products.products])
  const [encartes/* , setEncartes */] = useState(defaultEncartes)
  return (
    <div style={{ width: '100%', height: '100%' }} ref={ref}>
      <Builder.Site
        structure={[
          [<BoasVindas />, 'Anúncios'],
          ['Destaques', 'Ofertas', 'Promoções'],
          encartes.map(item => (
            <Encartes
              {...{ item }}
              {...{ dimensions }}
            />
          )),
          [<Orçamento
            item={{
              name: 'Orçamento',
              details: [
                { name: 'Total de Cestas', value: '2' },
                { name: 'Peso', value: '260 KG' },
                { name: 'Contém', value: '72 itens' },
                { name: 'Preço', value: 'R$ 378,00', highlight: true },
              ],
              images: orçamentoImages,
              removeItem: (index) => {
                const newList = [...orçamentoImages]
                newList.splice(index, 1)
                setOrçamentoImages(newList)
              }
            }}
            {...{ dimensions }} />, 'Formas de pagamento', 'Cálculo de frete', 'Compartilhar orçamento'],
          ['Sobre a MR', 'História'],
          ['Contato']
        ]}
      />
    </div>
  )
};